import {Component} from '@angular/core';
import {CdkDrag, CdkDragDrop, moveItemInArray, transferArrayItem} from '@angular/cdk/drag-drop';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'ngtrello';

  all: Tarefa[] = [];
  even: Tarefa[] = [];
  andamento: Tarefa[] = [];

  drop(event: CdkDragDrop<string[]>) {
    if (event.previousContainer === event.container) {
      moveItemInArray(event.container.data, event.previousIndex, event.currentIndex);
    } else {
      transferArrayItem(event.previousContainer.data,
        event.container.data,
        event.previousIndex,
        event.currentIndex);
    }
  }

  /** Predicate function that only allows even numbers to be dropped into a list. */
  evenPredicate(item: CdkDrag<number>) {
    return item.data % 2 === 0;
  }

  /** Predicate function that doesn't allow items to be dropped into a list. */
  noReturnPredicate() {
    return false;
  }

  test() {

  }

  addTarefa(list: string) {
    let sourceList = [];

    if (list === 'A') {
      sourceList = this.all;
    } else if (list === 'R') {
      sourceList = this.even;
    } else {
      sourceList = this.andamento;
    }

    const novaTarefa = {
      id: sourceList.length + 1,
      desc: '',
      editing: true
    };
    if (list === 'A') {
      this.all.push(novaTarefa);
    } else if (list === 'R') {
      this.even.push(novaTarefa);
    } else {
      this.andamento.push(novaTarefa);
    }

    setTimeout(function () {
      document.querySelector<HTMLInputElement>('#edt-' + list + '-' + novaTarefa.id).focus();
    });
  }

  checkIfIsValid(number: Tarefa, list: string) {

    if (!number.desc || number.desc.trim().length === 0) {

      if (list === 'A') {
        this.all = this.all.filter((t) => t.id !== number.id);
      } else if (list === 'R') {
        this.even = this.even.filter((t) => t.id !== number.id);
      } else {
        this.andamento = this.andamento.filter((t) => t.id !== number.id);
      }
    }


  }

  enterEditMode(number: Tarefa, list: string) {
    number.editing = true;

    setTimeout(function () {
      document.querySelector<HTMLInputElement>('#edt-' + list + '-' + number.id).focus();
    });
  }
}

export class Tarefa {
  id: number;
  desc: string;
  editing: boolean;
}

